.tagcloud {
    transition: transform 0.5s, opacity 0.5s, height 0.5s ease-in-out;
    opacity: 1;
    transform: scale(1);
    height: 100%; /* Set based on your cloud size */
}

.tagcloud-hidden {
    opacity: 0;
    transform: scale(1.5);
    height: 0px; /* Transition to no height */
    overflow: hidden; /* Prevent content from showing outside */
}

.fade-in {
    animation: fadeInAnimation 1s forwards;
}
.skills {
    display: grid;
    grid-template-columns: repeat(4, 1fr); /* Creates 4 columns, each taking up equal space */
    gap: 20px; /* Adds space between rows and columns */
    justify-content: center;
    width: 90%; /* Adjust the width as needed */
    margin: 0 auto; /* Centering the skills container */
    padding: 20px;
    background: #f4f4f4;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}


.skillBarContainer {
    width: 100%;
    margin: 10px 0; /* Consistent spacing around skill bars */
}

@keyframes fadeInAnimation {
    from { opacity: 0; transform: translateY(30px); }
    to { opacity: 1; transform: translateY(0); }
}


